.new-expense {
  &__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  &__control {
    label {
      font-weight: bold;
      margin-bottom: 0.5rem;
      display: block;
    }
    input {
      font: inherit;
      padding: 0.5rem;
      border-radius: 6px;
      border: 1px solid #ccc;
      width: 20rem;
      max-width: 100%;
    }
    }
  &__actions {
    text-align: right;
  }
}
